import { Component, OnInit } from '@angular/core';
import { GunService } from '../gun.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-roman',
  templateUrl: './roman.component.html',
  styleUrls: ['./roman.component.scss']
})
export class RomanComponent implements OnInit {
  loader = false;
  interest: number;
  topay: number;
  weekly: number;
  lister = [];
  userp: any;
  msg: any;
  reqa: Object;
  constructor(private gun: GunService, private router: Router) { }
  amount = 1000;
  tenure = 1;
  vat = 12;
  logs() {
    this.router.navigate(['/login']);
    window.localStorage.removeItem('username');
    window.localStorage.removeItem('_id');
  }
  ngOnInit() {
    this.loader = true;
    this.interest = this.amount / 100 * this.vat;
    this.topay = this.amount + this.amount / 100 * this.vat;
    this.weekly = this.topay / this.tenure;

    this.gun.inst(window.localStorage.getItem('username')).subscribe(data => {
      this.userp = data['graphql']['user'];
      this.lister.push(this.userp)
    });
    this.gun.getonerequests({ 'username': window.localStorage.getItem('username') }).subscribe(data => {
      this.reqa = data;
      this.eat(' Requests pending', 1);
      this.loader = false;
    });
  }
  delrequest(i) {
    this.loader = true;
    this.gun.delrequest({ '_id': i['_id'], 'username': i['username'] }).subscribe(data => {
      this.reqa = data;
      this.eat('Updated', 2);
      this.loader = false;
    });
  }
  getvat(e, b) {
    let v;
    if (b == 'a') {
      v = this.tenure;
    }
    else {
      v = e.target.value;
    }
    if (v == 1) {
      this.vat = 18;
    } else if (v > 1 && v <= 3) {
      this.vat = 20;
    }
    else if (v > 3 && v <= 5) {
      this.vat = 22;
    }
    else if (v > 5 && v <= 7) {
      this.vat = 24;
    } else if (v > 7 && v <= 9) {
      this.vat = 26;
    } else if (v > 9 && v <= 12) {
      this.vat = 28;
    }

    this.interest = this.amount / 100 * this.vat;
    this.topay = this.amount + this.amount / 100 * this.vat;
    this.weekly = this.topay / this.tenure;
  }
  requests() {
    const data = {
      amount: this.amount,
      weekly: this.weekly,
      vat: this.interest,
      tenure: this.tenure,
      pic: this.userp['profile_pic_url_hd'],
      fullname: this.userp['full_name'],
      total: this.topay,
      username: window.localStorage.getItem('username'),
      date: new Date(),
      _pId: window.localStorage.getItem('_id')
    }
    this.loader = true;
    this.gun.reqboard(data).subscribe(data => {
      this.loader = false;
      if (data['status'] === 'true') {
        this.eat(data['msg'], 2);
      } else {
        this.eat(data['msg'], 1);
      }
      this.gun.getonerequests({ 'username': window.localStorage.getItem('username') }).subscribe(data => {
        this.reqa = data;
      });
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    x.style.color = 'white';
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }
}
