import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GunService } from '../gun.service';

import { Location } from '@angular/common';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  srm: any;
  ngOnInit(): void {
  }
  regData: string;
  reg: boolean;
  logdata: boolean;
  loader = false;
  info: any;
  logo = false;
  goo = false;
  msg = "";
  enter = true;
  constructor(private loc: GunService, private router: Router, private location: Location) { }
  login = {
    username: "",
    password: "",
    cpassword: '',
    mobile: "",
    email: "",
    date: new Date(),
    update: 0
  };
  cpassword = "";

  logins() {
    this.enter = false;
    this.logdata = true;
    this.loader = true;
    this.loc.usercheck(this.login).subscribe(ccc => {
      if (ccc['status'] === false) {
        this.loc.login(this.login).subscribe(data => {
          this.logdata = false;
          if (data['status'] === true) {
            console.log(data, data['msg']);
            this.eat(data['msg'], 1);
            this.logdata = false; this.loader = false;
            window.localStorage.setItem("username", data['username']);
            window.localStorage.setItem("logged", data['username']);
            window.localStorage.setItem("update", data['update']);
            window.localStorage.setItem("_id", data['_id']);
            this.router.navigate(['/down']);
            window.localStorage.setItem("state", "true");
          } else {
            this.eat(data['msg'], 2);
            this.logdata = false;
            this.enter = true;
            this.loader = false;
            window.localStorage.setItem("state", "false");
          }
        });
      }
      else {
        this.logdata = false; this.enter = true;
        this.eat('Username not exists', 2); this.loader = false;
      }
    });
  }
  register() {
    this.enter = false;
    // delete this.login.lucky;
    // delete this.login.phrase;
    this.logdata = true;
    this.loc.usercheck(this.login).subscribe(ccc => {
      if (ccc['status'] === true) {
        if (this.login.password === this.login.cpassword) {
          this.loc.register(this.login).subscribe(data => {
            this.logdata = undefined;
            this.enter = true;
            this.reg = true;
            this.regData = data['msg'];
            if (data['status'] === true) {
              this.eat(data['msg'], 1);
              this.srm = true;
            } else {
              this.eat(data['msg'], 2);
            }
          });
        } else {
          this.logdata = undefined;
          this.enter = true;
          this.reg = true;
          this.eat('Password not matched', 2);
        }
      } else {
        this.logdata = undefined;
        this.enter = true;
        this.reg = true;
        this.eat(ccc['msg'], 2);
      }
    });
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function () {
      x.className = x.className.replace("show", "");
    }, 5000);
  }
}
